/* index.css */

/* Estilização do corpo da página */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f9f9f9;
}

/* Estilo do título */
h1 {
    color: #333;
}

/* Estilo do tabuleiro do jogo */
#board {
    display: grid;
    grid-template-columns: repeat(3, 100px);
    grid-template-rows: repeat(3, 100px);
    gap: 5px;
}

/* Estilo para cada célula do tabuleiro */
.cell {
    width: 100px;
    height: 100px;
    border: 1px solid #333;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px;
    cursor: pointer;
    background-color: #fff;
    transition: background-color 0.3s ease;
}

/* Estilo para a célula quando o mouse passa por cima */
.cell:hover {
    background-color: #f0f0f0;
}

/* Estilo de mensagem */
#roomMessage {
    margin: 20px 0;
    font-size: 18px;
    color: #007BFF;
}

/* Estilo para botões */
button {
    margin: 10px;
    padding: 10px 20px;
    font-size: 16px;
    color: #fff;
    background-color: #007BFF;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* Estilo para botões ao passar o mouse */
button:hover {
    background-color: #0056b3;
}

/* Estilo para ocultar o tabuleiro */
#gameBoard {
    display: none; /* Inicialmente oculto */
}
